import React, { Component, Fragment } from 'react';
import openSocket from 'socket.io-client';

import Post from '../../components/Posts/Post/PostThumbnail';
import Button from '../../components/Button/Button';
import FeedEdit from '../../components/Posts/PostEdit/PostEdit';
import Paginator from '../../components/Paginator/Paginator';
import Loader from '../../components/Loader/Loader';
import ErrorHandler from '../../components/ErrorHandler/ErrorHandler';
import './Feed.css';

import travel from '../../images/travel.png';

class Feed extends Component {
  state = {
    isEditing: false,
    posts: [],
    totalPosts: 0,
    editPost: null,
    status: '',
    postPage: 1,
    postsLoading: true,
    editLoading: false
  };

  componentDidMount() {
    this.loadPosts();
    const socket = openSocket(process.env.REACT_APP_TRAVEL_API);
    socket.on('posts', data=>{
      if(data.action === 'create'){
        this.addPost(data.post)
      } else if(data.action==='update'){
        this.updatePost(data.post)
      } else if(data.action==='delete'){
        this.loadPosts();
      }
    })
  }

  addPost = post=>{
    this.setState(prevState => {
      const updatedPosts = [...prevState.posts];
      if (prevState.postPage === 1) {
        if (prevState.posts.length >= 2) {
          updatedPosts.pop();
        }
        updatedPosts.unshift(post);
      }
      return {
        posts: updatedPosts,
        totalPosts: prevState.totalPosts + 1
      };
    });
  }

  updatePost = post =>{
    this.setState(prevState=>{
      const updatedPosts = [...prevState.posts];
      const updatedPostIndex = updatedPosts.findIndex(p=> p._id===post._id);
      if(updatedPostIndex > -1){
        updatedPosts[updatedPostIndex]=post;
      }
      return{
        posts: updatedPosts
      }
    })
  }

  loadPosts = direction => {
    if (direction) {
      this.setState({ postsLoading: true, posts: [] });
    }
    let page = this.state.postPage;
    if (direction === 'next') {
      page++;
      this.setState({ postPage: page });
    }
    if (direction === 'previous') {
      page--;
      this.setState({ postPage: page });
    }
    fetch(process.env.REACT_APP_TRAVEL_API+'feed/posts?page=' + page + '&ownPosts=' + this.props.ownPosts, {
      headers: {
        Authorization: 'Bearer '+this.props.token
      }
    })
      .then(res => {
        if (res.status !== 200) {
          throw new Error('Failed to fetch posts.');
        }
        return res.json();
      })
      .then(resData => {
        this.setState({
          posts: resData.posts.map(post=>{
            return {
              ...post,
              imagePath: post.imageUrl
            }
          }),
          totalPosts: resData.totalItems,
          lastPage: resData.lastPage,
          postsLoading: false
        });
      })
      .catch(this.catchError);
  };

  statusUpdateHandler = event => {
    event.preventDefault();
    fetch('URL')
      .then(res => {
        if (res.status !== 200 && res.status !== 201) {
          throw new Error("Can't update status!");
        }
        return res.json();
      })
      .then(resData => {
        console.log(resData);
      })
      .catch(this.catchError);
  };

  newPostHandler = () => {
    this.setState({ isEditing: true });
  };

  startEditPostHandler = postId => {
    this.setState(prevState => {
      const loadedPost = { ...prevState.posts.find(p => p._id === postId) };

      return {
        isEditing: true,
        editPost: loadedPost
      };
    });
  };

  cancelEditHandler = () => {
    this.setState({ isEditing: false, editPost: null });
  };

  finishEditHandler = postData => {
    this.setState({
      editLoading: true
    });
    console.log(postData)
    //Automatically sends headers
    const formData = new FormData();
    formData.append('title', postData.title);
    formData.append('content', postData.content);
    formData.append('image', postData.image);
    formData.append('longitude', postData.longitude);
    formData.append('latitude', postData.latitude);
    let url = process.env.REACT_APP_TRAVEL_API+'feed/posts';
    let method = 'POST';
    if (this.state.editPost) {
      url = process.env.REACT_APP_TRAVEL_API+'feed/post/'+this.state.editPost._id;
      method = 'PUT';
    }

    fetch(url, {
      method,
      headers: {
        Authorization: 'Bearer '+this.props.token
      },
      body: formData
    })
      .then(res => {
        if (res.status !== 200 && res.status !== 201) {
          throw new Error('Creating or editing a post failed!');
        }
        return res.json();
      })
      .then(resData => {
        const post = {
          _id: resData.post._id,
          title: resData.post.title,
          content: resData.post.content,
          creator: resData.post.creator,
          createdAt: resData.post.createdAt
        };
        this.setState(prevState => {
          return {
            isEditing: false,
            editPost: null,
            editLoading: false
          };
        });
      })
      .catch(err => {
        console.log(err);
        this.setState({
          isEditing: false,
          editPost: null,
          editLoading: false,
          error: err
        });
      });
  };

  statusInputChangeHandler = (input, value) => {
    this.setState({ status: value });
  };

  deletePostHandler = postId => {
    this.setState({ postsLoading: true });
    fetch(process.env.REACT_APP_TRAVEL_API+'feed/post/'+postId, {
      method: 'DELETE',
      headers: {
        Authorization: 'Bearer '+this.props.token
      }
    })
      .then(res => {
        if (res.status !== 200 && res.status !== 201) {
          throw new Error('Deleting a post failed!');
        }
        return res.json();
      })
      .then(resData => {
        console.log(resData);
        this.loadPosts();
      })
      .catch(err => {
        console.log(err);
        this.setState({ postsLoading: false });
      });
  };

  errorHandler = () => {
    this.setState({ error: null });
  };

  catchError = error => {
    this.setState({ error: error });
  };

  render() {
    return (
      <>
        {
          !this.props.ownPosts &&
          <section id="feed__hero">
          <div className="feed-hero__text">
            <h1>Adventure awaits!</h1>
            <p>Keep your travel journal, save your best addresses and tell the world about your last lucky find.</p>
            <div className="feed__control">
              <Button mode="raised" design="accent" onClick={this.newPostHandler}>
                Add a New Trip
              </Button>
            </div>
          </div>
          <div className="feed-hero__img">
            <img src={travel}/>
          </div>
        </section>
        }
        <ErrorHandler error={this.state.error} onHandle={this.errorHandler} />
        <FeedEdit
          editing={this.state.isEditing}
          selectedPost={this.state.editPost}
          loading={this.state.editLoading}
          onCancelEdit={this.cancelEditHandler}
          onFinishEdit={this.finishEditHandler}
        />
        <section className="feed">
          {this.state.postsLoading && (
            <div style={{ textAlign: 'center', marginTop: '2rem' }}>
              <Loader />
            </div>
          )}
          {this.state.posts.length <= 0 && !this.state.postsLoading ? (
            <p style={{ textAlign: 'center' }}>No posts found.</p>
          ) : null}
          {!this.state.postsLoading && (
            <Paginator
              onPrevious={this.loadPosts.bind(this, 'previous')}
              onNext={this.loadPosts.bind(this, 'next')}
              lastPage={this.state.lastPage}
              currentPage={this.state.postPage}
            >
              {
                !this.props.ownPosts 
                ?
                <>
                  <h2>Last Adventures Published</h2>
                  <p>{this.state.totalPosts} trips to discover!</p>
                </>
                :
                <>
                  <h2>Your last trips</h2>
                  <p>{this.state.totalPosts} trips registered</p>
                </>
              }
              <p className="feed__warning">Please note that, for security purposes, all posts published by guests will be subjected to validation by admin prior to publication on home page. Please contact me through my <a href="https://www.linkedin.com/in/lucie-ory-4aa50038/">linkedin account</a> to get admin status and your posts validated. As the purpose of this site is mainly to practice web development skills, guests post will be deleted each day to reduce data storing.</p>

              <div className="feed__posts">
                {this.state.posts.map(post => (
                  <Post
                    key={post._id}
                    id={post._id}
                    author={post.creator.name}
                    validated={post.validated}
                    is_author={post.creator._id===this.props.userId}
                    imageUrl={process.env.REACT_APP_TRAVEL_API+post.imageUrl}
                    date={new Date(post.createdAt).toLocaleDateString('en-US')}
                    title={post.title}
                    image={post.imageUrl}
                    content={post.content}
                    onStartEdit={this.startEditPostHandler.bind(this, post._id)}
                    onDelete={this.deletePostHandler.bind(this, post._id)}
                  />
                ))}
              </div>
            </Paginator>
          )}
        </section>
      </>
    );
  }
}

export default Feed;
